.productPageWrapper {
    display: flex;
    flex-direction: column;
    height: auto;
    max-width: 1200px;
    margin: auto;
    align-items: center;

}

.linkButtonsWrapper {
    display: flex;
    width: 1100px;
    margin: auto;
    align-items: center;
    max-height: 200px;
    flex-grow: 1;

}

.listItemsWrapper {
    display: flex;
    max-width: 100%;
    margin: auto;
    align-items: center;
    padding: 0px;

}

.listItemsWrapper li {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 10px 10px 10px;
    margin: 10px 100px 10px 100px;
    transition: 200ms ease-in-out;
}


.listItemsWrapper li a {
    border: 1px solid black;
    width: 82px;
    height: 76px;
    text-align: center;
    border-radius: 30%;
}

.listItemsWrapper li p {
    width: 120px;
    text-align: center;
    height: 10px;
    line-height: 12px;
    margin-bottom: 0px;
}

.iconStyle {
    width: 70px;
    height: 70px;
    text-align: center;
    color: rgba(0, 128, 0, 0.479);
}


.productsTemplateWrapper {
    display: flex;
    max-width: 1100px;
    margin: auto;
    text-align: center;
    margin-top: 130px;
    margin-bottom: 130px;
}

.imageContainer img {
    width: 248px;
    height: 400px;
}



.imageContainer {
    /* border: 5px solid black; */
    padding: 15px 10px 15px 10px;
    border-radius: 26px;
    background-color: rgba(248, 239, 239, 0.3);
    box-shadow: 2px 2px 24px 0px rgba(0, 0, 0, 0.37);
    transition: 1s;
}

/* Make it smoth transition when mouse is not on top of the element */
.imageContainer:hover {
    transition: 200ms ease-in-out;
    cursor: pointer;
    background-color: rgba(248, 239, 239, 0.555);
    box-shadow: 2px 2px 24px 0px rgba(0, 0, 0, 0.863);
}

