.imageContainer {
    margin: auto;
}

.imageContainer:nth-child(2) {
    margin-left: 60px;
    margin-right: 60px;
}


@media (max-width: 1100px) {
    .productPageWrapper {
        display: flex;
        flex-direction: column;
        max-width: 1000px;
        height: 100%;
        margin: auto;
    }

    .linkButtonsWrapper {
        max-width: 100%;
    }


    .listItemsWrapper li {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 10px 10px 10px 10px;
        margin: 10px 60px 10px 60px;
    }

    .productsTemplateWrapper {
        display: flex;
        align-items: center;
        flex-direction: column;
        max-width: 800px;
        margin: 130px auto auto auto;
        height: auto;
    }

    .imageContainer img {
        width: 248px;
        height: 260px;
        align-self: center;
        border-radius: 15px 0px 0px 15px;
        margin-right: 155px;
    }

    .imageContainer {
        display: flex;
        width: 700px;
        margin: auto;
        margin: 15px;
        padding: 15px;
        height: 250px;
    }

    .imageContainer:nth-child(2) {
        margin-left: 15px;
        margin-right: 15px;
    }
}


@media (max-width: 415px) {

    .productPageWrapper {
        display: flex;
        flex-direction: column;
        max-width: 100%;
        height: 100%;
        margin: auto;
    }


    .linkButtonsWrapper {
        display: flex;
        flex-direction: column;
        width: 300px;
        height: auto;
    }

    .listItemsWrapper {
        display: flex;
        flex-direction: column;
        
    }

    .productsTemplateWrapper {
        display: flex;
        align-items: center;
        flex-direction: column;
        max-width: 100%;
        margin: 30px auto auto auto;
        height: auto;
    }

    .imageContainer img {
        width: 196px;
        height: 270px;
        align-self: center;
        border-radius: 15px 0px 0px 15px;
        margin-right: auto;
    }

    .imageContainer {
        display: flex;
        width: 400px;
        margin: auto;
        margin: 20px;
        padding: 0px;
        height: 250px;
    }

    .imageContainer p {
        margin: auto;
        font-size: 25px;
    }   
}